html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  background: rgb(40, 44, 52);
  font-weight: 100;
  hr {
    border: 0;
    background: #555;
    height: 1px;
  }

  h1 {
    font-size: 100%;
    display: inline;
    margin: 0;
  }

  img {
    vertical-align: middle;
    border: 0;
  }

  a:focus {
    outline: none;
  }

  a.link {
    cursor: pointer;
  }
}

.wrap {
  height: 100%;
}

header {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-align-content: space-between;
  -ms-flex-line-pack: justify;
  align-content: space-between;
  -webkit-align-items: flex-start;
  -ms-flex-align: start;
  align-items: flex-start;
  position: absolute;
  top: 0;
  height: 60px;
  width: 100%;
  z-index: 4;
  font-family: Calibri, Helvetica, sans-serif;
  font-weight: bold;
  background: rgb(40, 44, 52);
  box-shadow: 0px 0 10px rgba(0, 0, 0, 0.4);

  &.light-theme {
    background: rgb(253, 246, 227);
    box-shadow: 0px 0 10px rgba(0, 0, 0, 0.28);
    .tools {
      i {
        color: rgb(40, 44, 52);
      }
      select {
        color: rgb(40, 44, 52);
        border: 1px solid #222;

        > option {
          background: white;
          color: #282b30;
        }
      }
      > {
        .logo {
          color: rgb(40, 44, 52);
        }
      }
    }
  }

  .storage-status {
    color: #fdf6e3;
    opacity: 0.5;
    font-style: italic;
    margin-top: auto;
    margin-bottom: auto;
    text-align: center;
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    align-self: auto;
    a {
      border: 2px solid rgba(255, 246, 223, 0.63);
      padding: 0 5px;
      border-radius: 4px;
      font-style: normal;
      opacity: 1;
    }
  }

  a.link {
    margin-left: 5px;
    margin-right: 5px;
  }

  .tools {
    font-family: Calibri, Helvetica, sans-serif;
    font-weight: bold;
    background: transparent;
    border-radius: 5px;
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    align-self: auto;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 20px;
    margin-right: 20px;

    select {
      color: white;
      background: transparent;
      padding: 5px;
      border: 1px solid #ccc;
      outline: transparent;
      margin-left: 5px;
      margin-right: 5px;

      > option {
