    // ul.options {
    //   list-style: none;
    //   padding: 0 5px 0 15px;
    //   margin: 2px 0;

    //   li {
    //     margin: 2px;

    //     p {
    //       margin: 2px;
    //     }
    //   }

    //   code {
    //     color: #c678dd;
    //   }
    // }

    i {
      color: #fdf6e3;
      font-size: 1.2em;
      width: 24px;
      height: 24px;
      margin-bottom: 5px;
    }

    > {
      a {
        margin-left: 5px;
      }

      .logo {
        color: #fdf6e3;
        font-size: 150%;
        line-height: 60px;
      }
      .logo-img {
        .jhi-logo {
          width: 30px;
        }
      }
      #tooltip {
        font-size: 90%;
        color: #807c72;
        position: fixed;
        top: 7px;
        right: 20px;
        text-align: right;
        background: rgb(40, 44, 52);
        border-radius: 2px;
