#linemarker {
  background: rgba(220, 50, 47, 0.5);
  position: absolute;
  left: 0;
  top: -30px;
  width: 45px;
  height: 22px;
  z-index: 4;
}

.CodeMirrorEditor {
  outline: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  box-sizing: border-box;
  border: 0;
  font-family: Consolas, Menlo, monospace;
  color: #657b83;
  // padding: 30px 0 0 40px;
  resize: none;
  opacity: 1;
  transition: opacity 0.3s;
}
.light-theme {
  .CodeMirrorEditor {
    background: #fdf6e3;
    color: #657b83;
    .CodeMirror-linenumber {
      color: #d4cebd;
    }
  }
  #linenumbers {
    background: #eee8d5;
    color: #d4cebd;
  }
  #error-tooltip{
    background: #eee8d5;
  }
  .canvas-tools {
    i {
      color: #282c34;
    }
  }
}

.CodeMirror {
  height: 96%;
  border: 0;
  width: 100%;
  margin: 60px 0 20px 0;
  line-height: 25px;
  font-size: 16px;
  font-family: Consolas, Monaco, monospace;
  opacity: 1;
  transition: opacity 0.3s;

  .CodeMirror-linenumber {
    color: #3e4350;
    padding-left: 12px;
  }
}

.canvas-mode {
  .CodeMirror {
    opacity: 0;
    transition: opacity 1s;
  }
  &.canvas-tools {
    opacity: 1;
    transition: opacity 0.3s;
  }
}

.canvas-tools {
  display: block;
  position: absolute;
  top: 75px;
  right: 25px;
  width: 24px;
  opacity: 0;
  z-index: 3;
  transition: opacity 1s;
  i {
    font-size: 1.5em;
    color: white;
  }

  a {
    line-height: 2.5em;
  }
}

#canvas-panner {
  position: absolute;
  z-index: 2;
  width: 40%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  top: 0;
  right: 20px;
  bottom: 0;
  cursor: move;
}

#linenumbers {
  position: absolute;
  top: 0;
  left: 0;
  width: 45px;
  height: 100%;
  background: rgb(32, 36, 45);
  color: #d4cebd;

  &.error {
    background: rgba(220, 50, 47, 0.3);
    z-index: 4;
  }
}

#canvas {
  position: absolute;
  cursor: move;
}

#error-tooltip {
  font-size: 90%;
  font-weight: bold;
  color: rgb(245, 96, 96);
  position: fixed;
  top: 75px;
  left: 45px;
  text-align: right;
  background: rgb(40, 44, 52);
  border-radius: 2px;
  padding: 0 5px;
  font-family: Calibri, Helvetica, sans-serif;
  z-index: 5;
}

a {
  color: #61afef;
  opacity: 0.75;
  text-decoration: none;

  &:active {
    color: #61afef;
    opacity: 0.75;
    text-decoration: none;
  }

  &:hover {
    color: #61afef;
    opacity: 1;
    text-decoration: none;
  }
}

.sidebar {
  display: none;
  position: absolute;
  z-index: 3;
  top: 0px;
  right: 0;
  bottom: 0;
  width: 375px;
  background-color: rgb(40, 44, 52);
  box-shadow: -3px 0 6px rgba(0, 0, 0, 0.28);

  .highlighter-rouge pre,
  code {
    color: #c678dd;
  }

  &.visible {
    display: block;
    overflow: auto;
  }

  .logo-background {
    position: fixed;
    top: 0;
    right: 20px;
    width: 300px;
    height: 70px;
    background: rgb(40, 44, 52);
  }

  .content {
    display: block;
    color: #9da5b4;
    font-family: Consolas, Monaco, monospace;
    padding: 70px 10px 200px 20px;

    p {
      font-family: Calibri, Helvetica, sans-serif;
    }

    label {
      display: block;
      font-family: Calibri, Helvetica, sans-serif;
      color: #93a1a1;
      margin-top: 1em;
    }

    h2 {
      font-size: inherit;
      display: block;
      font-family: Calibri, Helvetica, sans-serif;
      color: #93a1a1;
      margin-top: 1em;
    }

    b {
      display: inline-block;
      font-weight: normal;
      width: 40%;
      font-family: Calibri, Helvetica, sans-serif;
      color: #93a1a1;
    }
  }
  .invert {
    -webkit-filter: grayscale(100%) brightness(1.5) invert(100%);
    filter: grayscale(100%) brightness(1.5) invert(100%);
  }
  &.light-theme {
    background-color: rgb(253, 246, 227);
    box-shadow: -3px 0 6px rgba(0, 0, 0, 0.28);
    .content {
      color: #586e75;
    }
    hr {
      background: #0004;
    }
    .invert {
      -webkit-filter: none;
      filter: none;
    }
  }
}

/* Styles for dialog window */

button.btn {
  border: 1px solid #b9b3b3;
  color: rgb(40, 44, 52);
  background: transparent;
  padding: 6px 15px;
  border-radius: 2px;
  cursor: pointer;

  &:hover {
    background: #b9b3b3;
  }
}

input.upload {
  margin: 5px 0 25px;
}

.modal {
  > .header {
    width: 98%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
  }
  > .content {
    width: 100%;
    padding: 10px 5px;
  }
  > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
    button {
      margin: 5px;
    }
  }
  > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
  }
}
.popup-content {
  border-radius: 5px;
  padding: 0.5rem;
}

.popup-content {
  background-color: rgb(41, 45, 62);
  color: #fff;
  border: 1px solid #9a9595;
}

.popup-arrow > svg {
  color: rgb(41, 45, 62);
  stroke-width: 2px;
  stroke: #9a9595;
  stroke-dasharray: 30px;
  stroke-dashoffset: -54px;
}

.light-theme {
  .popup-content {
    background-color: #fff;
    color: #000;
    border: 1px solid #d7d7d7;
  }

  .popup-arrow > svg {
    color: #fff;
    stroke-width: 2px;
    stroke: #d7d7d7;
    stroke-dasharray: 30px;
    stroke-dashoffset: -54px;
  }
}

/**
 * Fade-zoom animation for first dialog
 */

.my-mfp-zoom-in {
  /* start state */
  .zoom-anim-dialog {
    opacity: 0;
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    -o-transform: scale(0.8);
    transform: scale(0.8);
  }
  /* animate in */
  &.mfp-ready .zoom-anim-dialog {
    opacity: 1;
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }

  /* animate out */
  &.mfp-removing .zoom-anim-dialog {
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    -o-transform: scale(0.8);
    transform: scale(0.8);
    opacity: 0;
  }
  /* Dark overlay, start state */

  &.mfp-bg {
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
  }

  /* animate in */
  &.mfp-ready.mfp-bg {
    opacity: 0.8;
  }
  /* animate out */
  &.mfp-removing.mfp-bg {
    opacity: 0;
  }
}

/**
 * Fade-move animation for second dialog
 */

.my-mfp-slide-bottom {
  /* at start */
  .zoom-anim-dialog {
    opacity: 0;
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    -webkit-transform: translateY(-20px) perspective(600px) rotateX(10deg);
    -moz-transform: translateY(-20px) perspective(600px) rotateX(10deg);
    -ms-transform: translateY(-20px) perspective(600px) rotateX(10deg);
    -o-transform: translateY(-20px) perspective(600px) rotateX(10deg);
    transform: translateY(-20px) perspective(600px) rotateX(10deg);
  }
  /* animate in */
  &.mfp-ready .zoom-anim-dialog {
    opacity: 1;
    -webkit-transform: translateY(0) perspective(600px) rotateX(0);
    -moz-transform: translateY(0) perspective(600px) rotateX(0);
    -ms-transform: translateY(0) perspective(600px) rotateX(0);
    -o-transform: translateY(0) perspective(600px) rotateX(0);
    transform: translateY(0) perspective(600px) rotateX(0);
  }
  /* animate out */
  &.mfp-removing .zoom-anim-dialog {
    opacity: 0;
    -webkit-transform: translateY(-10px) perspective(600px) rotateX(10deg);
    -moz-transform: translateY(-10px) perspective(600px) rotateX(10deg);
    -ms-transform: translateY(-10px) perspective(600px) rotateX(10deg);
    -o-transform: translateY(-10px) perspective(600px) rotateX(10deg);
    transform: translateY(-10px) perspective(600px) rotateX(10deg);
  }

  /* Dark overlay, start state */
  &.mfp-bg {
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
  }
  /* animate in */
  &.mfp-ready.mfp-bg {
    opacity: 0.8;
  }
  /* animate out */
  &.mfp-removing.mfp-bg {
    opacity: 0;
  }
}
